import React from 'react';

const Contactbann = () => {
    return (
        <div className='contactbann'>
            <div className="insidecontact">
                <div className="principcontact">
                    <h2>Viajar a Madagascar</h2>
                    <p>Nuestra agencia  local y todo el equipo le dan la bienvenida a esta pagina web. Si planea viajar a Madagascar pronto, estamos aquí para ayudarle, ofrecerle paquetes turísticos, viajes personalizados o solo consejos, si planea viajar por su cuenta. <br />
                    No dude en contactarnos a través de los formularios o mensajes instantáneos, nos encantaría responderle</p>
                    <button>Nous contacter</button>
                </div>
            </div>
            
        </div>
    );
};

export default Contactbann;