import React from "react";

const Formulaire = (tour) => {
  return (
    <div className="mytourform">
      <input
        type="hidden"
        name="abouttour"
        value={"id circuit: " + tour.id + ", nom circuit : " + tour.nomcircuit}
      />
      <label htmlFor="nombre">Nombre</label>
      <input type="text" name="nombre" id="nombre" placeholder="Exemple: Paul" />
      <label htmlFor="correo">Correo</label>
      <input type="email" name="correo" id="correo" placeholder="Exemple: paul@mail.com" />
      <label htmlFor="whatsapp">Telefono / Whatsapp</label>
      <input type="email" name="whatsapp" id="whatsapp" placeholder="Exemple: +32 0 01 02 03 04" />
      <label htmlFor="numeropersonas">Número de personas *</label>
      <input type="text" name="numeropersonas" id="numeropersonas" placeholder="Exemple: 4" />
      <label htmlFor="salida">Fecha de salida *</label>
      <input type="date" name="salida" id="salida" />
      <label htmlFor="mensaje">Mensaje</label>
      <textarea
        name="Mensaje"
        id=""
        cols="30"
        rows="5"
        placeholder="Escriba su mensaje aquí."
      ></textarea>
      <input type="submit" value="Enviar" />
    </div>
  );
};

export default Formulaire;
